// import React, { useState } from 'react'
import React, { useMemo } from 'react';

import queryString from 'query-string';

// import { heroes } from '../data/heroes';
import { HeroCard } from '../heroes/HeroCard';
import { useForm } from '../../hooks/useForm';
import { useLocation } from 'react-router-dom';
import { getHeroesByName } from '../../selectors/getHeroesByName';

export const SearchScreen = ({ history }) => {

    const location = useLocation();
    const { q = "" } = queryString.parse(location.search);

    
    const [formValues, handleInputChange] =  useForm({
        search: q
    });
    
    const { search } = formValues;

    // const [heroesFiltered, setHeroes] = useState(getHeroesByName( search ));
    // const heroesFiltered = getHeroesByName( search );
    const heroesFiltered = useMemo(() => getHeroesByName( q ), [q]);

    const handleSearch = (e) => {
        e.preventDefault();
        console.log(formValues.search);
        console.log(formValues.search.length);
        history.push(`?q=${formValues.search}`);
        // if (formValues.search.length >= 1){
        //     setHeroes(previousHeros => {
        //         return previousHeros.filter(hero => {
        //             return hero.superhero.toLowerCase().includes(formValues.search.toLowerCase());
        //         });
        //     })
        // }else{
        //     setHeroes(heroes)
        // }

    }
 
    return (
        <div>
            <h1>Search Screen</h1>

            <div className="row">
                <div className="col-5">
                    <h4>Search Form</h4>
                    <hr />
                    <form onSubmit={handleSearch}>
                        <input 
                            type="text"
                            placeholder="Find your hero"
                            name="search"
                            className="form-control"
                            onChange={handleInputChange}
                            value={ search }
                        />
                        <button className="btn m-1 btn-block btn-outline-primary">
                            Search...
                        </button>
                    </form>
                </div>
                <div className="col-7">
                    <h4>Results</h4>
                    <hr />
                    {
                        (q === '') && 
                        <div className="alert alert-info">
                            Search a hero...
                        </div>
                    }
                    {
                        (q !== '' && heroesFiltered.length === 0) && 
                        <div className="alert alert-danger">
                            There is not a hero with "{q}"
                        </div>
                    }
                    {
                        heroesFiltered.map(hero => (
                            <HeroCard 
                                key={ hero.id }
                                {...hero} 
                            />
                        ))
                    }
                </div>
            </div>
        </div>
    )
}
